.color-picker {
  position: absolute;
  top: 4em;
  left: 0;
}

.color-picker label {
  display: block;
  color: white;
  background-color: #333;
  border: solid 1px black;
  margin-bottom: 5px;
}

section {
  margin-bottom: 15px;
}

.dropdown {
  display: grid;
  grid-template-columns: 100px 50px;
  gap: 10px;
}

.colorbox {
  width: 25px;
  height: 25px;
  border: 1px solid white;
}

button {
  margin-left: 10px;
  border-radius: 3px;
}

.settings {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.settings div {
  padding: 10px;
  border-radius: 5px;
  color: #000000;
  background-color: #ffffff;
  position: relative;
  width: 300px;
  margin: 0 auto;
  top: 30%;
}

.settings div p {
  margin: 0;
}
