@import url(https://fonts.googleapis.com/css?family=Ubuntu:400,700&display=swap);
* {
  box-sizing: border-box;
}

:root {
  --bg-color: #201f1f;
  --font-color: #f4f4f4;
  --main-color: #12abdb;
  --board-color: #0b6daa;
  --item-color: #252423;
}

body {
  background: #201f1f;
  background: var(--bg-color);
  color: #f4f4f4;
  color: var(--font-color);
  font-family: "Ubuntu", Verdana, sans-serif;
  /* font-weight: 700; */
}

.main-content {
  max-width: 1000px;
  width: 100%;
  margin: 4em auto 0;
  overflow: hidden;
  border-radius: 3px;
}

.title {
  background: #12abdb;
  background: var(--main-color);
  color: #fff;
  padding: 30px 10px;
  grid-column: span 5;
  text-align: center;
  font-size: 72px;
}

.card {
  background: #0b6daa;
  background: var(--board-color);
  padding: 10px;
  display: grid;
  grid-gap: 3px;
  grid-template-rows: repeat(5, 110px);
  grid-template-columns: repeat(5, 1fr);
  text-transform: uppercase;
}

.item {
  background: #252423;
  background: var(--item-color);
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  font-size: 12px;
  line-height: 1.35;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.item {
  padding: 15px;
}

.checkmark {
  display: block;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.bingo-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.bingo-overlay span {
  padding: 5px;
  color: #12abdb;
  color: var(--main-color);
  position: relative;
  font-size: 150px;
  top: 30%;
  -webkit-animation: glow 1s ease infinite;
          animation: glow 1s ease infinite;
}

@-webkit-keyframes glow {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe,
      0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 0.2vw #082180,
      0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@keyframes glow {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe,
      0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 0.2vw #082180,
      0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@media only screen and (max-width: 600px) {
  .main-content,
  body {
    margin: 0;
  }

  .title {
    font-size: 50px;
  }

  .item {
    padding-left: 2px;
    padding-right: 2px;
    font-size: 10px;
  }

  .card {
    padding-left: 0;
    padding-right: 0;
  }

  .bingo-overlay span {
    font-size: 100px;
  }
}

@-webkit-keyframes hvr-push {
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes hvr-push {
  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
.hvr-push {
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.hvr-push:hover,
.hvr-push:focus,
.hvr-push:active {
  -webkit-animation-name: hvr-push;
  animation-name: hvr-push;
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}

.color-picker {
  position: absolute;
  top: 4em;
  left: 0;
}

.color-picker label {
  display: block;
  color: white;
  background-color: #333;
  border: solid 1px black;
  margin-bottom: 5px;
}

section {
  margin-bottom: 15px;
}

.dropdown {
  display: grid;
  grid-template-columns: 100px 50px;
  grid-gap: 10px;
  gap: 10px;
}

.colorbox {
  width: 25px;
  height: 25px;
  border: 1px solid white;
}

button {
  margin-left: 10px;
  border-radius: 3px;
}

.settings {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.settings div {
  padding: 10px;
  border-radius: 5px;
  color: #000000;
  background-color: #ffffff;
  position: relative;
  width: 300px;
  margin: 0 auto;
  top: 30%;
}

.settings div p {
  margin: 0;
}

